import {Injectable} from '@angular/core';
import {Subject} from './model/subject.model';
import {Group} from './model/group.model';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class GroupService {

  constructor(private http: HttpClient) {
  }

  getAllGroups() {
    return this.http.get<Group[]>('https://lab-univer.herokuapp.com/admin/group/all');
  }

  getGroupById(id: string) {
    return this.http.get('https://lab-univer.herokuapp.com/group/' + id,);
  }

  makeMark(mark: number, idStudent: string, idGroup: string, idSubject) {

    const body = {
      mark, idStudent, idGroup, idSubject
    };
    console.log(body);
    return this.http.post('https://lab-univer.herokuapp.com/make-mark', body).subscribe();
  }
}
