import {Component, OnInit} from '@angular/core';
import {Label} from 'ng2-charts';
import {ChartDataSets, ChartOptions, ChartType} from 'chart.js';
import {SubjectService} from '../subject.service';
import {GroupService} from '../group.service';

@Component({
  selector: 'app-statistic',
  templateUrl: './statistic.component.html',
  styleUrls: ['./statistic.component.css']
})
export class StatisticComponent implements OnInit {
  public barChartOptions: ChartOptions = {
    responsive: true,
    // We use these empty structures as placeholders for dynamic theming.
    scales: {xAxes: [{}], yAxes: [{}]},
    plugins: {
      datalabels: {
        anchor: 'end',
        align: 'end',
      }
    }
  };
  subjects: [];
  groups = [];

  public barChartLabels: Label[] = [];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;

  public barChartData: ChartDataSets[] = [
    {data: [1, 2, 3, 4, 5], label: 'mark'}];

  constructor(private subjectService: SubjectService, private groupService: GroupService) {

  }

  ngOnInit() {
    this.getAllSubjects();
    this.openSubject();
    this.getAllGroups();
  }

  getAllSubjects() {
    this.subjectService.getAll().subscribe((data: any) => {
        this.subjects = data.subjects;
      }
    );
  }


  getAllGroups() {
    this.groupService.getAllGroups().subscribe((data: any) => {
        this.groups = data.groups;
        console.log(this.groups);
        data.groups.forEach(el => {
          this.barChartLabels.push(el.number);
        });
      }
    );
  }

  public openSubject(): void {
    const data = [
      Math.round(Math.random() * 5),
      3.3,
      5,
      (Math.random() * 5),
      3.3,
      (Math.random() * 5),
      5];
    this.barChartData[0].data = data;
  }

  onChange(value: any) {
    this.openSubject();
  }
}
